.terminal42--tus {
  margin: 5px 0;
}
.terminal42--tus__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.terminal42--tus__item {
  display: grid;
  grid-template: 1fr 1fr/auto 1fr;
  gap: 10px;
  position: relative;
  margin-top: 10px;
}
.terminal42--tus__preview {
  grid-row: 1/3;
  grid-column: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  max-height: 100px;
}
.terminal42--tus__preview img {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
}
.terminal42--tus__name {
  grid-area: 1/2;
  display: flex;
  align-items: center;
}
.terminal42--tus__progress {
  grid-area: 2/2;
  position: relative;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 0;
}
.terminal42--tus__progress > span {
  position: relative;
  z-index: 10;
}
.terminal42--tus__progress > div {
  position: absolute;
  inset: 0 auto 0 0;
  width: 0;
  padding: 5px 0;
  background: var(--info-bg, rgba(0, 100, 148, 0.15));
  transition: all 2s;
  z-index: 2;
}
.terminal42--tus__progress.success {
  background: var(--confirm-bg, rgba(88, 155, 14, 0.15));
}
.terminal42--tus__progress.error {
  background: --error-bg, rgba(204, 51, 51, 0.15);
}
.terminal42--tus__remove {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  padding: 0 5px;
  font-size: 20px;
  color: var(--red, #c33);
  text-align: center;
  background: transparent;
  border: none;
  cursor: pointer;
}